<template>
  <div class="has-background-cwhite-1">
    <div class="tile is-vertical">
      <!--顶部上层展示项目的数据内容-->
      <div class="tile">
        <div class="tile is-parent is-vertical is-3">
          <article class="tile is-child notification padding-0">
            <card-component class="tile is-child" icon="chart-arc">
              <div>
                <div class="level">
                  <div class="level-item">
                    <!-- Content -->
                    <div class="tile is-child notification is-cgreen-4 padding-0">
                      <h1 class="title is-2 has-text-centered">
                        <b>{{ basicinfo.completed }}</b>
                      </h1>
                      <p class=" has-text-centered ">
                        {{ TASKSTATUS_CN.COMPLETED }}
                      </p>
                    </div>
                  </div>
                  <div class="level-item">
                    <!-- Content -->
                    <div class="tile is-child notification is-cyellow-1 padding-0">
                      <h1 class="title is-2 has-text-centered">
                        <b>{{ basicinfo.exec }}</b>
                      </h1>
                      <p class=" has-text-centered ">
                        {{ TASKSTATUS_CN.EXEC }}
                      </p>
                    </div>
                  </div>
                </div>
                <hr class="margin-02 has-background-cblue-2" />
                <div>
                  <div class="level">
                    <div class="box level-item">
                      <p class="has-text-left mt-3">{{ TASKSTATUS_CN.TOTAL }}</p>
                      <p class="subtitle is-3 is-italic ml-3 has-text-cgreen-4">
                        {{ basicinfo.total }}
                      </p>
                    </div>
                    <div class="box level-item">
                      <p class="has-text-left mt-3">{{ TASKSTATUS_CN.COVER }}</p>
                      <p class="subtitle is-3 is-italic ml-3 has-text-cblue-4">{{ basicinfo.cover }} %</p>
                    </div>
                  </div>
                  <div class="level">
                    <div class="box level-item">
                      <p class="has-text-left mt-3">{{ TASKSTATUS_CN.HOLD }}</p>
                      <p class="subtitle is-3 is-italic ml-3 has-text-cgray-1">
                        {{ basicinfo.hold }}
                      </p>
                    </div>
                    <div class="box level-item">
                      <p class="has-text-left mt-3">{{ TASKSTATUS_CN.BLOCK }}</p>
                      <p class="subtitle is-3 is-italic ml-3  has-text-cred-2">
                        {{ basicinfo.block }}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </card-component>
          </article>
          <article class="tile is-child notification padding-0" :style="zoom">
            <b-carousel
              class="tile is-child"
              :autoplay="true"
              :interval="1500"
              :arrow="false"
              :indicator="false"
              :pause-info="false"
            >
              <b-carousel-item v-for="(item, i) in carousels" :key="i">
                <section class="hero">
                  <div class="hero-body">
                    <div>
                      <card-widget
                        leftorright="left"
                        class="tile is-child"
                        type="is-success"
                        icon="chart-timeline-variant"
                        :message="item"
                      ></card-widget>
                    </div>
                  </div>
                </section>
              </b-carousel-item>
            </b-carousel>
          </article>
          <article class="tile is-child notification padding-0" :style="zoom">
            <b-carousel
              class="tile is-child"
              :autoplay="true"
              :interval="1500"
              :arrow="false"
              :indicator="false"
              :pause-info="false"
            >
              <b-carousel-item v-for="(item, i) in carousels" :key="i">
                <section class="hero">
                  <div class="hero-body">
                    <div>
                      <card-widget
                        class="tile is-child"
                        type="is-success"
                        icon="chart-timeline-variant"
                        :message="item"
                        leftorright="right"
                      ></card-widget>
                    </div>
                  </div>
                </section>
              </b-carousel-item>
            </b-carousel>
          </article>
        </div>
        <div class="tile">
          <div class="tile is-parent is-vertical">
            <article class="tile is-child notification padding-0">
              <apexchart :options="linechart.Options" :series="linechart.series" />
            </article>
            <article class="tile is-child notification padding-0">
              <apexchart :options="columnchart.Options" :series="columnchart.series" />
            </article>
          </div>
        </div>
        <div class="tile">
          <div class="tile is-parent is-vertical">
            <article class="tile is-child notification padding-0">
              <p class="title">上1</p>
              <p class="subtitle">不知道放什么，随便放</p>
              <div class="content">
                <!-- Content -->
              </div>
            </article>
            <article class="tile is-child notification padding-0">
              <p class="title">上2</p>
              <p class="subtitle">不知道放什么，随便放</p>
              <div class="content">
                <!-- Content -->
              </div>
            </article>
          </div>
        </div>
        <div class="tile is-parent">
          <article class="tile is-child notification padding-0">
            <p class="title">旁边</p>
            <p class="subtitle">不知道放什么，随便放</p>
            <div class="content">
              <!-- Content -->
            </div>
          </article>
        </div>
      </div>
      <!--顶部上层展示项目的数据内容-->
      <div class="tile is-parent">
        <article class="tile is-child notification padding-0">
          <personal-task-table
            ref="ptt"
            class="padding-1"
            :alldata="activetabtasklist"
            :showtaskid="true"
            :allowsearch="true"
            :width="180"
            @updateTaskData="updateTaskData"
            @changeSel="changeSel"
          />
        </article>
        <article class="tile is-child ification padding-0 is-4"></article>
      </div>
    </div>
  </div>
</template>

<script>
import { TASKSTATUS_EN, TASKSTATUS_CN } from '../../scripts/internal'
import { getProjectBasicInfo, getCarousels, getPorjectStatusByDate, totaltask } from '../../scripts/common'
import CardComponent from '@/components/Card/CardComponent'
import CardWidget from '@/components/Card/CardWidget'
import PersonalTaskTable from '@/components/Table/PersonalTaskTable'

export default {
  name: 'ViewProject',
  components: {
    CardComponent,
    CardWidget,
    PersonalTaskTable
  },
  props: {
    /**
     * 项目名称
     */
    projectname: String
  },
  data() {
    return {
      /**
       * 缩放大小
       */
      zoom: 'zoom:65%',
      /**
       * 本地
       * 缩放状态
       */
      TASKSTATUS_CN: TASKSTATUS_CN,
      /**
       * 子父参值分离
       */
      name: '',
      /**
       * 项目基本信息
       */
      basicinfo: {},
      /**
       * 当前项目任务列表
       */
      projecttasklist: [],
      /**
       * 轮播消息
       * NOTICE: 这边暂时借用一下执行日志的操作
       * TIPS: 轮播消息应该是轮播的本项目内的单个任务(TASK)
       */
      carousels: [],

      linechart: {
        series: [],
        Options: {
          chart: {
            height: 80,
            width: 100,
            type: 'line',
            toolbar: {
              show: false
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          xaxis: {
            type: 'number'
          }
        }
      },

      columnchart: {
        series: [],
        Options: {
          chart: {
            type: 'bar',
            height: 80,
            width: 100,
            toolbar: {
              show: false
            }
          },
          plotOptions: {
            bar: {
              horizontal: false,
              columnWidth: '55%',
              endingShape: 'rounded'
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            show: true,
            width: 2,
            colors: ['transparent']
          },
          xaxis: {
            categories: ['一', '二', '三', '四', '五', '六', '七']
          },
          yaxis: {
            title: {
              text: '个(TASKS)'
            }
          },
          fill: {
            opacity: 1
          },
          tooltip: {
            y: {
              formatter: function(val) {
                return val + '个(task)'
              }
            }
          }
        }
      },

      /**
       * 活动的tab内容
       */
      activetabtasklist: [],
      /**
       *所有数据
       */
      totaltasklist: [],

      /**
       * 已选择的task
       */
      selectedtask: {}
    }
  },
  watch: {
    projectname(newVal) {
      this.name = newVal
      this.init()
    }
  },
  mounted() {
    this.init()
    this.seriesFunc()
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      this.basicinfo = getProjectBasicInfo(this.name)
      this.carousels = getCarousels(this.name)

      this.totaltasklist = totaltask() // 提交给总任务
      this.changeTab(TASKSTATUS_EN.EXEC) // 默认只显示执行中的
    },

    /**
     * 处理图表数据
     * NOTICE: 1.新建和完成写入line表
     * NOTICE: 2.其他的写入radio表
     * TIPS: 这边可能变为插入一个跑马灯->类似carousels
     */
    seriesFunc() {
      const tempdate = getPorjectStatusByDate(this.name)
      // console.log(tempdate)
      this.linechart.series = [] // 初始化

      this.columnchart.series = [] // 初始化

      // 处理数据
      tempdate.forEach(element => {
        if (element.type === 'new' || element.type === 'completed') {
          const temparray = [] // 获取data数据
          element.values.forEach(value => {
            temparray.push(value.value)
          })
          const template = {
            name: element.type,
            data: temparray
          } // 赋值，拼接
          this.linechart.series.push(template) // 送入正主
        } else if (element.type === 'exec' || element.type === 'hold' || element.type === 'block') {
          const temparray = [] // 获取data数据
          element.values.forEach(value => {
            temparray.push(value.value)
          })
          const template = {
            name: element.type,
            data: temparray
          } // 赋值，拼接
          this.columnchart.series.push(template) // 送入正主
        }
      })
      // sconsole.log(this.linechart)
    },

    /**
     * 改变tab sheet
     */
    changeTab(value) {
      // 设置活动的任务tab的为空
      this.activetabtasklist = []
      // 根据状态分拆到各个tab内，当tab进行更改时执行
      if (value === TASKSTATUS_EN.TOTAL) {
        this.activetabtasklist = this.totaltasklist
      } else {
        for (let index = 0; index < this.totaltasklist.length; index++) {
          const element = this.totaltasklist[index]
          if (element.taskstatus.toLowerCase() === value) {
            this.activetabtasklist.push(element)
          }
        }
      }
    },

    /**
     * 从子组件传递至上部组件的数据
     */
    updateTaskData(data) {
      /** 替换数据 */
      this.totaltasklist[data.id] = data
    },

    /**
     * 子组件新选择了task，更新
     */
    changeSel(data) {
      this.selectedtask = data
    }
  }
}
</script>

<style lang="scss" scoped>
.tile {
  &.is-parent {
    padding: 0rem;
  }
}

.tile {
  &.is-vertical & > .tile.is-child:not(:last-child) {
    margin-bottom: 0.5rem !important;
  }
}

.padding-0 {
  padding: 0rem;
}

.margin-02 {
  margin: 0.2rem;
}

.hero-body {
  padding: 0rem;
}
.padding-1 {
  padding: 1rem;
}
</style>
